
.foot_container{
    top:0;
    display:flex;
    flex-direction: row;
    align-items:center;
    background-color: #373A40;
    justify-content: space-around;
    overflow-x:none;
}

.Foot_box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Foot{
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    padding:30px;
    gap:20px;
}
.social_mediaLinks{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items: center;
}
.svg_list{
    display:flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;
    gap:30px;
}

.boy_image{
    height: 350px;
    box-shadow: 2px 2px 5px white;
    margin:20px;
}

svg{
    fill:rgb(222, 222, 236);
    height:30px;
}
.svg_class:hover{
    fill:#6a98f4;
    transform: scale(1.03);
    box-shadow: 2px 2px 10px black;
    transition: 0.2s;
}
.Links{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Links_list{
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
    gap:40px;
}

.link{
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    color:white;
    font-family:"Open Sans", sans-serif ;
}
.link:hover{
    all:none;
    color:rgb(107, 176, 237);
}

.svg_links{
    all:none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.right_claim{
    display: flex;
    justify-content: center;
    align-items: center;
}
.copyrigt{
    font-size: 15px;
    font-weight: bold;
    color:white;
    font-family:"Open Sans", sans-serif ;
    text-align: center;
}
@media (max-width:1050px){
    .foot_container{
    justify-content: center;
}
.Foot{
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
  
}
.Links_list{
    flex-direction: column;
    justify-content: center;
    gap:50px;
}
.svg_list{
    flex-direction: column;
}
.Foot_box{
    flex-direction: column;
    justify-content:center;
    align-items: center;
}
    .boy_image{
        display:none;
    }
    .link{
        font-size:15px;
    }

    .svg_name{
        font-size:13px;
    }
    svg{
     height:30px;

    }
}