.stuff{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
.tech_box{
    display: flex;
    flex-direction:row;
    align-items: center;
    gap:60px;
    width:70%;
}
.about_me{
    display: flex;
     justify-content: center;
     align-items: center;
}
.about_text{
    font-size: 15px;
    font-weight: bold;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;
    text-align: center;
    max-width: 60%;
}
.progress_line1{
    width:80%;
    height: 5px;
    left:0;
    background:  linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);;
    border-radius:30px;
}
.progress_line{
    width:70%;
    height: 5px;
    left:0;
    background:  linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);;
    border-radius:30px;
}
.progress_line2{
    width:50%;
    height: 5px;
    left:0;
    background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);;
    border-radius:30px;
}
.techs{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width:100%;
        padding-bottom:20px;
}
.Tech_head{    
    text-align: center;
    font-weight:800;
    color:#686D76;
    font-family:"Open Sans", sans-serif;
}
.Images_box{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap:30px;
    padding:20px;
    width:50%;
    overflow-x:auto;
    box-shadow:1px 1px  5px black;
    border-radius:20px;
}

::-webkit-scrollbar{
    background-color:rgb(236, 243, 244);
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}
.Images_box::-webkit-scrollbar-thumb{
    background:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    border-radius:20px;
}
::-webkit-scrollbar-thumb{
    background: #686D76;
    border-radius:5px;
}
