.page_heading{
    font-size:40px;
    font-family:"Poppins", sans-serif;
    font-weight:800;
    text-align: center;
    background:  linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);;
    -webkit-background-clip: text;
    background-clip:text;
    color: transparent;
}
.line{
    width:130px;
    border: 7px dotted  #d05e12;
    border-bottom: none;
}
.img_field{
    display: flex;
    justify-content: center;
    padding:20px;
}
.pic{
    height:300px;
    width:300px;
    border-radius: 50%;
}
.pic:hover{
    transform:scale(1.04);
    border:2px solid #d05e12;
    transition: 0.4s;
}
.about_me{
    display: flex;
     justify-content: center;
     align-items: center;
}
.about_text{
    font-size: 15px;
    font-weight: bold;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;
    text-align: center;
    max-width: 60%;
}
