body{
   margin:0;
   display: flex;
   flex-direction: column;

}
header{
    top:0;
    background-color:#FF7D29;
    display: flex;
    flex-direction: column;
    max-height: auto;
    height:800px;
    width:100%;
}
.Nav_bar{
   position: fixed;
   display: flex;
   justify-content: center;
   width:95%;
   z-index:2;
}
.container{
    display: flex;
    flex-direction:column;
    justify-content: center;
    width:95%;
    height:60px;
    margin-top:20px;
    background-color:white;
    border-radius:30px;
    box-shadow: 2px 2px 4px black ;
}
nav{
    display: flex;
    flex-direction:row;
    justify-content: space-between;
    align-items: center;
    
}

.Lists{
    display:flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
    gap:15px;
}
.list_items{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap:17px;
}
.new_class{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap:17px;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    font-size: 15px;
    font-weight: bold;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;
}
a:hover{
    background:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;

}
.heading{
    font-size:22px;
    font-weight: 800;
    font-family:"Poppins", sans-serif;
    padding-left:25px;
    color:#d05e12;
    cursor:pointer;

}
.nav_icon{
    height: 25px;
    width:25px;
    opacity:0;
    display: none;
}
.btn_contact{
    background-color: #FF7D29;
    color: white;
    height: 35px;
    width: 90px;
    font-size: 13px;
    font-weight: bold;
    border: none;
    border-radius:20px;
}

.active{
    padding-bottom:2px;
    border-bottom:2px solid rgb(240, 103, 103);
    color:#c95407;
}
.intro{
    margin-top:70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:space-around;
    height: 100%;
    gap:30px;
}
.Text_box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 40%;
    text-align: center;
}
.info{
    font-size:40px;
    font-family:"Poppins", sans-serif;
    font-weight:800;
    color: white;
}
.description{
    font-family:"Open Sans", sans-serif ;
    font-size: 18px;
    font-weight:600;
    color:white;
}
.boy_img{
    margin-top: 40px;
    height: 350px;
    padding:10px;
}
.Image_box{
    box-shadow: 2px 2px 2px brown;

}
.boy_img:hover{
    height: 370px;
    transition:350ms;
}
.Btn{
  background-color:white;
  color:#d05e12;
  border:none;
  border-radius: 30px;
  height: 50px;
  width:150px;
  font-size:16px;
  font-weight:700;
  margin-top: 20px;
  margin-bottom: 30px;
}
.btn_link{
    text-decoration: none;
    color:#686D76;
}
.Btn:hover{
    background-color: #686D76;
    color:white;
    transition:300ms;
    box-shadow:2px 2px 2px rgb(16, 13, 8);

}
.btn_link:hover{
    text-decoration: none;
    color:white;
}
@media (max-width:1050px){
    .Lists{
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
    .list_items{
       flex-direction:column;
       margin-top:280px;
       background-color: #EF9C66;
       align-items:center;
       justify-content:center;
       padding:0;
       position: fixed;
       right:0px;
       width:200px;
       height:200vh;
    }
    .link_lists{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width:100%;
        padding-bottom:2px;

    }
    .nav_icon{
        all:unset;
        height:30px;
        z-index:2;
    }
    .a{
        color:white
    }
    
    .info{
        font-size:25px;
        
}
.intro{
    flex-direction: column;
}

.boy_img{
    margin-top: 40px;
    height: 150px;
}
.boy_img:hover{
    height: 170px;
}
.description{
    font-size:14px;
}
.new_class{
    display: none;
}
}