
.Wrap_box{
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
    padding-bottom:30px;
}
.Wrapper{
    display:flex;
    flex-direction:row;
    align-items:center;
    gap:5px;
    padding:5px;
    width:80%;
    overflow-x:scroll;
    padding-bottom:10px;

}
.Wrapper::-webkit-scrollbar-thumb{
    background:linear-gradient(90deg, rgb(8, 4, 86) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);

}