.contact_Details{
    display: flex;
    align-items: center;
    justify-content: center;
}
.about_me{
    display: flex;
     justify-content: center;
     align-items: center;
}
.about_text{
    font-size: 15px;
    font-weight: bold;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;
    text-align: center;
    max-width: 60%;
}
.span{
    font-size: 12px;
    font-weight:800;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;
}
.Contact_info{
    font-size: 12px;
    color:#686D76;
    font-family:"Open Sans", sans-serif ;

}
.contact_bar{

    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
}
form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:20px;
    background-color:#526D82;
    padding:30px;
    border-radius:10px;
    width:33%;
}
.name_field {
    margin-top:25px;
 }

input{
    outline: none;
    border:none;
    width:95%;
    padding-left:15px;
    border-radius:5px;
    height:40px;
    background-color: #9DB2BF;
    color:white;
    font-size:13px;
    font-family:"Open Sans", sans-serif ;
}
textarea{
    outline: none;
    border:none;
    width:95%;
    height:100px;
    padding:15px;
    border-radius:5px;
    background-color: #9DB2BF;
    color:white;
    font-size:13px;
    font-family:"Open Sans", sans-serif ;
}
.Submit_btn{
    background-color: #27374D;
    color: white;
    height:40px;
    min-width:45%;
    font-size: 13px;
    font-weight: bold;
    border: none;
    border-radius:15px;
}
::placeholder {
    color:white;
    opacity: 1; 
  }